<template>
	<div class="portfolio">
		<div class="container ">
			<div class="avatar-wrapper">
			</div>
			<h1 class="portfolio-title">Check my works</h1>
			<transition name="portfolio">
				 <div  class="portfolio-wrapper" v-if="!showWork"> <!-- HERE -->
					<div class="portfolio-item" @click="showPortfolio(work.id)"
					     v-for="work in works" :key="work.id"
					>
						<img :src="getImgUrl(work.img[0])" alt="work" class="portfolio-image">
						<div class="portfolio-intro">
							<h3 class="portfolio-item-content portfolio-item-title">{{work.title}}</h3>
							<span class="portfolio-item-content portfolio-item-suptitle">{{work.subtitle}}</span>
						</div>
					</div>
					 <button class="btn-primary"
					         @click="showMore"
					         style="margin-bottom: 25px"
					         v-if="$store.getters.works.length>counter">Load more</button>
				</div>
			</transition>
			<transition name="portfolio">
			<PortfolioItem v-if="showWork"
				               @closeTab="showWork=false"/>
			</transition>

		</div>
	</div>
</template>
<script>
    import PortfolioItem from '../components/PortfolioItem'

    export default {
        name: 'Portfolio',
        metaInfo(){
            return {
                title:this.$store.state.pageTitle + " | Works"
            }
        },
		    data:()=>({
				    showWork:false,
				    counter:3,
		    }),
        methods: {
            getImgUrl(pic) {
                return require('../assets/' + pic)
            },
            showPortfolio(id){
                this.$store.commit('switchActive',id)
		            this.showWork=true
            },
            showMore(){
                this.$store.getters.works.length>this.counter+1 ? this.counter+=2 : ++this.counter
            }
        },
		    computed:{
            works(){
                return (this.$store.getters.works).slice(0,this.counter)
            }
		    },
        components: {
            PortfolioItem
        },
    }
</script>
